.congif{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 84%;
  background: #181a1b;
  z-index: 100;
}

.gif {
  position: absolute;
  top: 50%;
  left: 50%;;
  transform: translate(-50%,-50%);

  width: 30px;
  height: auto;

  transition: opacity .3s;
}
